<template>
  <el-drawer
    :title="title"
    :visible="value"
    append-to-body
    destroy-on-close
    :before-close="beforeClose"
    :close-on-press-escape="false"
    :direction="direction"
    :size="size"
    :wrapper-closable="false"
    @open="initSelect"
  >
    <el-form ref="form" inline :model="form" :rules="rules" label-width="auto">
      <el-scrollbar class="scroll-drawer">
        <div class="form-box">
          <el-form-item label="盘点类型：" prop="type">
            <el-select
              v-model="form.type"
              filterable
              clearable
              placeholder="请选择盘点类型"
              style="width: 178px;"
              @change="typeChange"
            >
              <el-option
                v-for="(item, index) in [{name: '全盘', id: 1},{name: '动态盘', id: 2},{name: '抽盘', id: 3}]"
                :key="index"
                :label="item.name"
                :value="item.id"
              />
            </el-select>
          </el-form-item>
          <el-form-item label="仓库：" prop="warehouseId">
            <el-select
              v-model="form.warehouseId"
              filterable
              clearable
              placeholder="仓库名称"
              style="width: 178px;"
              @change="handleSelectChange"
            >
              <el-option
                v-for="(item,index) in warehousePullList"
                :key="index"
                :label="item.name"
                :value="item.id"
              />
            </el-select>
          </el-form-item>
          <el-form-item v-if="form.type === 3" label="物料：">
            <el-link
              type="primary"
              style="width: 178px"
              @click="openMaterialDlg"
            >
              自选物料
            </el-link>
          </el-form-item>
          <el-row v-if="form.type === 3 && form.materialList.length" style="margin-bottom: 18px;padding-left: 120px;box-sizing: border-box;">
            <el-tag
              v-for="(tag, index) in form.materialList"
              :key="index"
              hit
              class="my-el-tag"
              closable
              :disable-transitions="false"
              @close="handleClose(tag, index)"
            >
              {{ tag.materialCode }}
            </el-tag>
          </el-row>
          <el-form-item v-if="form.type === 2" label="盘点时间范围：" required :style="{margin: '0 10px 18px 0'}">
            <el-form-item prop="startDate" :style="{margin: '0'}">
              <el-date-picker
                v-model="form.startDate"
                type="date"
                placeholder="开始日期"
                value-format="yyyy-MM-dd"
                style="width: 178px;"
                @change="form.abortDate = ''"
              />
            </el-form-item>
            <span style="margin: 0 5px;">-</span>
            <el-form-item prop="abortDate" :style="{margin: '0'}">
              <el-date-picker
                v-model="form.abortDate"
                type="date"
                placeholder="截止日期"
                value-format="yyyy-MM-dd"
                style="width: 178px;"
                :picker-options="{disabledDate: (time) => time.getTime() < new Date(form.startDate).getTime()}"
                @change="form.checkDate = ''"
              />
            </el-form-item>
          </el-form-item>
          <el-form-item v-if="[1, 3].includes(form.type)" label="截止日期" prop="abortDate">
            <el-date-picker
              v-model="form.abortDate"
              type="date"
              placeholder="截止日期"
              value-format="yyyy-MM-dd"
              style="width: 178px;"
              @change="form.checkDate = ''"
            />
          </el-form-item>
          <el-form-item v-if="form.type" label="最后执行日期：" prop="checkDate">
            <el-date-picker
              v-model="form.checkDate"
              type="date"
              style="width: 178px;"
              value-format="yyyy-MM-dd"
              placeholder="请选择最后执行日期"
              :picker-options="{disabledDate: (time) => time.getTime() < new Date(form.abortDate).getTime()}"
            />
          </el-form-item>
          <el-form-item label="库管员：" prop="storekeeperName">
            <template>
              <div class="selected-wrapper">
                <el-input
                  v-model.trim="form.storekeeperName"
                  disabled
                  placeholder="请选择库管员"
                  style="width: 143px;"
                />
                <el-button
                  plain
                  type="primary"
                  style="margin-left:5px;padding-left:0;padding-right:0;width:30px"
                  icon="el-icon-search"
                  @click="userDialogVisible = true, personType = 'storekeeper'"
                />
              </div>
            </template>
          </el-form-item>
          <el-form-item label="采办管理人员：" prop="purchaseUserName">
            <template>
              <div class="selected-wrapper">
                <el-input v-model.trim="form.purchaseUserName" disabled placeholder="请选择采办管理人员" style="width: 143px;" />
                <el-button
                  plain
                  type="primary"
                  style="margin-left:5px;padding-left:0;padding-right:0;width:30px"
                  icon="el-icon-search"
                  @click="userDialogVisible = true, personType = 'purchase'"
                />
              </div>
            </template>
          </el-form-item>
          <el-form-item label="业务单位人员：" prop="businessUserName">
            <template>
              <div class="selected-wrapper">
                <el-input
                  v-model.trim="form.businessUserName"
                  disabled
                  placeholder="请选择业务单位人员"
                  style="width: 143px;"
                />
                <el-button
                  plain
                  type="primary"
                  style="margin-left:5px;padding-left:0;padding-right:0;width:30px"
                  icon="el-icon-search"
                  @click="userDialogVisible = true, personType = 'business'"
                />
              </div>
            </template>
          </el-form-item>
          <el-form-item label="联系电话：" prop="phone">
            <el-input v-model.trim="form.phone" placeholder="输入电话" clearable style="width: 178px;" />
          </el-form-item>
          <el-form-item label="地点：" prop="address">
            <el-input v-model.trim="form.address" placeholder="输入地点" clearable style="width: 178px;" />
          </el-form-item>
        </div>
      </el-scrollbar>
      <div class="drawer-footer">
        <el-button :loading="loading" type="primary" @click="handleConfirm('form')">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
        <el-button @click="beforeClose">取 消</el-button>
      </div>
    </el-form>
    <origin-user-tree
      id="null"
      ref="addUserDialog"
      v-model="userDialogVisible"
      axios-module="sys"
      get-user-url="/sys/user/listByOffice"
      :multiple="false"
      :multiple-detail="false"
      :extra-info="{ excludeNames: ['检验'] }"
      @submitUserList="submitUserList"
    />
    <!-- 物料选择 -->
    <material-dialog
      v-model="materialVisible"
      :extra-info="{warehouseId: form.warehouseId, materialIdList: ids}"
      @confirm="onSelectedMaterial"
    />
  </el-drawer>
</template>

<script>
import { mapGetters } from 'vuex'
import { addListApi as addApi, updateListApi as updataApi } from '@/apis/modules/warehouse/check/Plan'
import { getWarehouseSapPullListApi } from '@/apis/modules/base/basewmsWarehouse'
import { promiseWrapper } from '@/utils/utils'
import { originUserChooseMixins } from '@/mixins/tableColumnChoose'
import MaterialDialog from '@/components/ModalSelect/MaterialForm'
import moment from 'moment'
moment.locale('zh-cn')

const INIT_FORM = () => ({
  type: '', // 类型(1.月度，2.年中，3.年末，4.随机)
  checkDate: '', // 最后执行日期
  startDate: '', // 开始日期
  abortDate: '', // 截止日期
  warehouseId: '', // 仓库id
  phone: '', // 电话
  address: '', // 地址
  storekeeper: '', // 库管员id
  storekeeperName: '', // 库管员名称
  purchaseUserId: '', // 采办管理人员id
  purchaseUserName: '', // 采办管理人员名称
  businessUserId: '', // 业务管理人员id
  businessUserName: '', // 业务管理人员名称
  materialList: [] // 物料列表(盘点类型为随机的时候物料才可以被选择)
})
export default {
  name: 'AddPlanDialogComs',
  components: {
    MaterialDialog
  },
  mixins: [originUserChooseMixins],
  props: {
    value: {
      required: true,
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    direction: {
      type: String,
      default: 'rtl'
    },
    size: {
      type: [Number, String],
      default: 640
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => {
        return { ...INIT_FORM() }
      }
    }
  },
  data() {
    return {
      dicts: [],
      form: INIT_FORM(),
      loading: false,
      rules: {
        type: [
          { required: true, message: '请选择盘点类型', trigger: 'change' }
        ],
        checkDate: [
          { required: true, message: '请选择最后执行日期', trigger: 'change' }
        ],
        startDate: [
          { required: true, message: '请选择开始日期', trigger: 'change' }
        ],
        abortDate: [
          { required: true, message: '请选择截止日期', trigger: 'change' }
        ],
        warehouseId: [
          { required: true, message: '请选择仓库', trigger: 'change' }
        ],
        phone: [
          { required: true, message: '请输入联系电话', trigger: 'blur' }
        ],
        address: [
          { required: true, message: '请输入地点', trigger: 'blur' }
        ],
        storekeeperName: [
          { required: true, message: '请选择库管员', trigger: 'change' }
        ],
        purchaseUserName: [
          { required: true, message: '请选择采办管理人员', trigger: 'change' }
        ],
        businessUserName: [
          { required: true, message: '请选择业务单位人员', trigger: 'change' }
        ]
      },
      personType: '',
      materialVisible: false,
      warehousePullList: [] // 仓库下拉列表
    }
  },
  computed: {
    ...mapGetters('app', {
      navbarWarehouseCheckedList: 'navbarWarehouseCheckedList'
    }),
    ids() {
      return this.form.materialList.map(item => item.materialId)
    },
    // 月度默认时间
    monthlyTime() {
      // 开始时间(当月第一天)
      const startDate = moment().startOf('month').format('YYYY-MM-DD')
      // 结束时间(当月最后一天)
      const abortDate = moment().endOf('month').format('YYYY-MM-DD')
      return {
        startDate,
        abortDate
      }
    },
    // 年中默认时间
    yearMidTime() {
      // 开始时间(当年第一天)
      const startDate = moment().startOf('year').format('YYYY-MM-DD')
      // 结束时间(年中最后一天)
      const abortDate = moment(new Date()).month(5).date(1).endOf('month').format('YYYY-MM-DD')
      return {
        startDate,
        abortDate
      }
    },
    // 年末默认时间
    yearEndTime() {
      // 开始时间(当年第一天)
      const startDate = moment().startOf('year').format('YYYY-MM-DD')
      // 结束时间(当年最后一天)
      const abortDate = moment().endOf('year').format('YYYY-MM-DD')
      return {
        startDate,
        abortDate
      }
    }
  },
  watch: {
    dialogForm: {
      handler: function(value) {
        if (this.editType === 'add') {
          this.form = { ...INIT_FORM() }
        } else {
          this.form = {
            ...value
          }
        }
      },
      immediate: true
    }
  },
  mounted() {
  },
  methods: {
    initSelect() {
      this.getWarehousePullList()
    },
    // 选择人员
    submitUserList(userInfo) {
      const { id, nickName } = userInfo
      if (this.personType === 'storekeeper') { // 库管员
        this.form.storekeeper = id
        this.form.storekeeperName = nickName
      } else if (this.personType === 'purchase') { // 采办管理人员
        this.form.purchaseUserId = id
        this.form.purchaseUserName = nickName
      } else if (this.personType === 'business') { // 业务单位人员
        this.form.businessUserId = id
        this.form.businessUserName = nickName
      }
    },
    handleConfirm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.form.type === 3 && !this.form.materialList.length) {
            this.$message({
              message: '请选择物料',
              type: 'warning'
            })
            return
          }
          this.loading = true
          const Api = {
            add: addApi,
            modify: updataApi
          }
          const params = {
            ...this.form,
            materialList: this.form.type !== 3 ? [] : this.form.materialList
          }
          Api[this.editType](params)
            .then(res => {
              const { code } = res
              if (!code) {
                this.$message({
                  message: `${this.title}成功`,
                  type: 'success'
                })
                this.$emit('submit')
                this.beforeClose()
              }
              this.loading = false
            }).catch(err => {
              console.log(err)
              this.loading = false
            })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    beforeClose(done) {
      this.$emit('input', false)
      this.$refs.form.resetFields()
      this.form = INIT_FORM()
      typeof done === 'function' && done()
    },
    // Tip 选择库位
    async chooseKuwei({ currentPosition, storageId }) {
      const diag = await promiseWrapper(this.$confirm(`是否选择（${currentPosition}）?`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }))
      if (!diag.success) return
      this.$emit('confirm', {
        currentPosition,
        storageId
      })
      this.$emit('input', false)
    },
    // Tip 选择物料
    onSelectedMaterial(list) {
      if (!list.length) return
      this.form.materialList = list.map(item => ({
        id: item.id,
        materialName: item.name, // 物料名称
        materialCode: item.code, // 物料编码
        materialId: item.id // 物料id
      }))
    },
    // Tip 打开物料弹窗
    openMaterialDlg() {
      if (!this.form.warehouseId) {
        this.$message({
          message: '请先选择仓库',
          type: 'warning'
        })
        return
      }
      this.materialVisible = true
    },
    // Tip 盘点类型改变
    typeChange(type) {
      if (!type) {
        this.form.startDate = ''
        this.form.abortDate = ''
        this.form.checkDate = ''
      } else {
        if ([1, 3].includes(type)) {
          this.form.startDate = ''
        }
      }
    },
    // Tip 关闭标签
    handleClose(tag, index) {
      this.form.materialList.splice(index, 1)
    },
    // 仓库下拉数据
    getWarehousePullList() {
      return new Promise((resolve, reject) => {
        getWarehouseSapPullListApi().then(res => {
          const { code, data } = res
          if (!code) {
            this.warehousePullList = data || []
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 下拉框选中值改变的回调
    handleSelectChange(bindVal) {
      this.form.materialList = []
    }
  }
}
</script>

<style lang='scss' scoped>
::v-deep:focus {
    outline: 0;
}
.scroll-drawer {
    height: calc(100vh - 150px);
    .form-box {
      width: 100%;
      box-sizing: border-box;
      padding: 0 12px;
    }
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
}
.drawer-footer {
  text-align: right;
}
::v-deep .my-el-tag.el-tag {
  margin: 0 5px 5px;
}
</style>

