<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <!-- <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="billNumber" class="fl">
          <el-input v-model.trim="query.billNumber" placeholder="调拨单号查询" class="filter-item" style="width: 178px;" />
        </el-form-item>
      </el-form>
      <el-button
        class="filter-item"
        type="success"
        icon="el-icon-search"
        @click="searchInit"
      >搜索</el-button>
      <el-button
        class="filter-item"
        type="warning"
        icon="el-icon-refresh-left"
        @click="resetForm('filterItem')"
      >重置</el-button>
      <el-button
        class="filter-item"
        type="primary"
        icon="el-icon-plus"
        @click="handleAdd"
      >新增</el-button>
    </div> -->
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="120"
        :refresh-key="refreshKey"
        :expand-list="expandList"
        @refresh="searchInit"
        @rowClick="handleRowClick"
      >
        <!-- <template slot-scope="scope">
          <el-button type="warning" @click="handleModify(scope.row)">编辑</el-button>
          <el-button type="danger" @click="handleDelete(scope.row)">删除</el-button>
        </template> -->
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />

    <!-- 新增 -->
    <!-- <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="editType"
      :title="dialogTitle"
      :type="type"
      @submit="init"
    /> -->

  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import paginationPage from '@/mixins/paginationPage'
// import EditDialog from './Dialog'
import { getListApi, deleteListApi } from '@/apis/modules/warehouse/check/Plan'
import tableHeadSet from '@/mixins/tableHeadSet'

export default {
  name: 'WarehouseDischarge',
  components: {
    // EditDialog
  },
  mixins: [paginationPage, tableHeadSet],
  data() {
    return {
      title: '历史记录',
      loading: false,
      query: {
        // billNumber: ''
      },
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'type_dictText',
          label: '盘点类型',
          align: 'center',
          // width: 120,
          // minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'checkDate',
          label: '盘点时间',
          align: 'center',
          minWidth: '200',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'warehouseName',
          label: '仓库名称',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'userNames',
          label: '库管员/采办管理人员/业务单位人员',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'phone',
          label: '联系电话',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'address',
          label: '地区',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnSets: [],
      dialogForm: {},
      editType: 'add',
      dialogVisible: false
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `新增${this.title}信息`
      } else {
        return `编辑${this.title}信息`
      }
    },
    ...mapGetters('app', {
      navbarWarehouseCheckedList: 'navbarWarehouseCheckedList'
    })
  },
  watch: {
    navbarWarehouseCheckedList: {
      handler() {
        this.init()
      },
      deep: true
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      this.loading = true
      const params = {
        page: {
          ...this.pageInfo
        },
        ...this.query,
        warehouseIds: this.navbarWarehouseCheckedList.map(item => item.id),
        isHistory: '1'
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      console.log(row)
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
      // 更新分页
      this.init('clean')
    },
    // 批量删除
    handleDelete(params, item) {
      this.$confirm('此操作将永久删除选中信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        deleteListApi({
          id: item.id
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '成功',
              message: '删除成功'
            })
            this.init()
          }
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    handleAdd() {
      this.editType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    handleModify(item) {
      this.dialogForm = { ...item }
      this.editType = 'modify'
      this.dialogVisible = true
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
